                                                    <g id="np_chevron_1189476_225773" transform="translate(18 17) rotate(-180)">
                                                        <path id="Path" d="M0,8.5l.9.994L7.678,17,10,15.011,4.114,8.5,10,1.989,7.678,0,.9,7.505Z" transform="translate(0)" fill="#225773"></path>
                                                    </g>
